ay: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  gap: 10px;\r\n  padding: 2px 0;\r\n  position: relative;\r\n}\r\n\r\n.title {\r\n  position: relative;\r\n  font-family: \"Aclonica\", sans-serif;\r\n  font-size: 26px;\r\n}\r\n\r\n.image-logo {\r\n  width: 40px;\r\n  height: 40px;\r\n}\r\n\r\n.image-logo img {\r\n  width: 100%;\r\n}\r\n\r\naside {\r\n  width: 320px;\r\n  padding: 12px;\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: flex-start;\r\n  grid-area: aside;\r\n  gap: 15px;\r\n}\r\n\r\n.top {\r\n  display: grid;\r\n  grid-template-columns: 1fr 1fr;\r\n  width: 100%;\r\n  gap: 15px;\r\n}\r\n\r\n.message {\r\n  background-color: var(--bg-container);\r\n  display: flex;\r\n  border-radius: 10px;\r\n  flex-direction: column;\r\n  padding: 10px;\r\n  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;\r\n}\r\n\r\n.day-message {\r\n  font-size: 15px;\r\n}\r\n\r\n#day-number {\r\n  font-size: 36px;\r\n  margin-top: -16px;\r\n  position: relative;\r\n}\r\n\r\n#day-number:after {\r\n  content: \"\";\r\n  position: absolute;\r\n  bottom: 6px;\r\n  width: 20px;\r\n  height: 5px;\r\n  background-color: var(--underline-color);\r\n  left: 0;\r\n}\r\n\r\n.image {\r\n  background-image: url(\"../images/image-quote.png\");\r\n  height: auto;\r\n  width: 100%;\r\n  border-radius: 10px;\r\n  background-size: cover;\r\n  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;\r\n}\r\n\r\n.bottom {\r\n  background-color: var(--bg-container);\r\n  border-radius: 10px;\r\n  width: 100%;\r\n  height: 100%;\r\n  padding: 5px;\r\n  display: flex;\r\n  flex-direction: column;\r\n  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;\r\n}\r\n\r\n.home,\r\n.projects {\r\n  font-size: 24px;\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 10px;\r\n  justify-content: center;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.home span,\r\n.projects span {\r\n  position: relative;\r\n}\r\n\r\n.home span:after {\r\n  content: \"\";\r\n  position: absolute;\r\n  bottom: 5px;\r\n  l